import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { Responsive, WidthProvider } from 'react-grid-layout';
import { Grid, Button, TextField, FormControl, InputLabel, Select, MenuItem, IconButton } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import DraggableResizableImage from './DraggableResizableImage';
import csvData from './ExpData.csv';
import 'react-grid-layout/css/styles.css';
import 'react-resizable/css/styles.css';

const ResponsiveGridLayout = WidthProvider(Responsive);

const Heatmap = () => {
  const [data, setData] = useState({ x: [], y: [], z: {} });
  const [x, setX] = useState([]);
  const [y, setY] = useState([]);
  const [selectedZ, setSelectedZ] = useState([]); // Track multiple selected heatmaps
  const [savedLayouts, setSavedLayouts] = useState([]);
  const [zOptions, setZOptions] = useState([]);
  const [layoutName, setLayoutName] = useState('');
  const [selectedLayout, setSelectedLayout] = useState('');
  const [layouts, setLayouts] = useState({ lg: [] });
  const [boxCount, setBoxCount] = useState(1); // Start with 1 for the initial heatmap

  const parseCSV = useCallback((csvData) => {
    const rows = csvData.split('\n').map(row => row.split(','));
    const headers = rows[0];
    const dataRows = rows.slice(1).filter(row => row.length === headers.length);
    return { headers, rows: dataRows };
  }, []);

  const loadData = useCallback(() => {
    fetch(csvData)
      .then(response => response.text())
      .then(csv => {
        const { headers, rows } = parseCSV(csv);
        const desalIndex = headers.indexOf('Desal 1 base cost');
        const disposalIndex = headers.indexOf('Disposal cost');

        const xValues = new Set();
        const yValues = new Set();
        const zData = {};
        headers.slice(2).forEach(header => {
          zData[header] = [];
        });

        rows.forEach(row => {
          const xValue = parseFloat(row[desalIndex]);
          const yValue = parseFloat(row[disposalIndex]);

          xValues.add(xValue);
          if (yValue >= 1 && yValue <= 5) {
            yValues.add(yValue);
            headers.slice(2).forEach((header, index) => {
              if (!zData[header]) zData[header] = [];
              zData[header].push({ x: xValue, y: yValue, value: parseFloat(row[index + 2]) });
            });
          }
        });

        const uniqueX = Array.from(xValues);
        const uniqueY = Array.from(yValues);

        Object.keys(zData).forEach(header => {
          const zMatrix = Array.from({ length: uniqueY.length }, () => Array(uniqueX.length).fill(null));
          zData[header].forEach(point => {
            const xIndex = uniqueX.indexOf(point.x);
            const yIndex = uniqueY.indexOf(point.y);
            if (xIndex !== -1 && yIndex !== -1) {
              zMatrix[yIndex][xIndex] = point.value;
            }
          });
          zData[header] = zMatrix;
        });

        setData({ x: uniqueX, y: uniqueY, z: zData });
        setX(uniqueX);
        setY(uniqueY);
        setZOptions(headers.slice(2));
      });
  }, [parseCSV]);

  useEffect(() => {
    loadData();
  }, [loadData]);

  const handleZChange = (event) => {
    const newZ = event.target.value;
    setSelectedZ(newZ);
    setLayouts((prevLayouts) => {
      const newLayout = newZ.map((zKey, index) => ({
        i: `box-${index}`,
        x: (index * 2) % 12,
        y: Infinity,
        w: 4,
        h: 4,
      }));
      return { lg: newLayout };
    });
    setBoxCount(newZ.length);
  };

  const handleSaveLayout = () => {
    if (layoutName.trim() !== '') {
      setSavedLayouts(prevLayouts => [
        ...prevLayouts,
        { name: layoutName, layout: layouts.lg }
      ]);
      setLayoutName('');
    }
  };

  const handleLayoutSelect = (event) => {
    const selected = event.target.value;
    const layout = savedLayouts.find(layout => layout.name === selected);
    if (layout) {
      setSelectedLayout(selected);
      setLayouts({ lg: layout.layout });
    }
  };

  const handleDeleteLayout = (name) => {
    setSavedLayouts(prevLayouts => prevLayouts.filter(layout => layout.name !== name));
    if (selectedLayout === name) {
      setSelectedLayout('');
      setLayouts({ lg: [] });
    }
  };

  const onLayoutChange = useCallback((newLayout) => {
    setLayouts({ lg: newLayout });
  }, []);

  return (
    <div>
      <Grid container spacing={2} alignItems="center" justifyContent="flex-start">
        <Grid item>
          <FormControl variant="outlined" style={{ margin: '16px', minWidth: '200px' }}>
            <InputLabel id="heatmap-select-label">Select Heatmaps</InputLabel>
            <Select
              labelId="heatmap-select-label"
              multiple
              value={selectedZ}
              onChange={handleZChange}
              label="Select Heatmaps"
            >
              {zOptions.map((option, index) => (
                <MenuItem key={index} value={option}>
                  {option}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
        <Grid item>
          <TextField
            label="Layout Name"
            value={layoutName}
            onChange={(e) => setLayoutName(e.target.value)}
            variant="outlined"
            style={{ margin: '16px', minWidth: '200px' }}
          />
        </Grid>
        <Grid item>
          <Button variant="contained" color="primary" onClick={handleSaveLayout} style={{ margin: '16px' }}>
            Save Layout
          </Button>
        </Grid>
        <Grid item>
          <FormControl variant="outlined" style={{ margin: '16px', minWidth: '200px' }}>
            <InputLabel id="saved-layout-select-label">Saved Layouts</InputLabel>
            <Select
              labelId="saved-layout-select-label"
              value={selectedLayout}
              onChange={handleLayoutSelect}
              label="Saved Layouts"
            >
              {savedLayouts.map((layout, index) => (
                <MenuItem key={index} value={layout.name}>
                  {layout.name}
                  <IconButton onClick={() => handleDeleteLayout(layout.name)} size="small">
                    <DeleteIcon />
                  </IconButton>
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
      </Grid>
      <ResponsiveGridLayout
        className="layout"
        layouts={layouts}
        breakpoints={{ lg: 1200, md: 996, sm: 768, xs: 480, xxs: 0 }}
        cols={{ lg: 12, md: 10, sm: 6, xs: 4, xxs: 2 }}
        rowHeight={30}
        onLayoutChange={onLayoutChange}
        onResizeStop={onLayoutChange}
        onDragStop={onLayoutChange}
        draggableHandle=".handle"
        isResizable={true}
        isDraggable={true}
      >
        {selectedZ.map((zKey, index) => (
          <div key={`box-${index}`} data-grid={layouts.lg[index]}>
            <div className="handle" style={{ width: '100%', height: '100%' }}>
              <DraggableResizableImage
                data={[
                  {
                    x: x,
                    y: y,
                    z: data.z[zKey] || [],
                    type: 'heatmap',
                    colorscale: 'Viridis',
                    colorbar: {
                      title: zKey,
                      titleside: 'right'
                    }
                  },
                ]}
                layout={{
                  title: zKey,
                  xaxis: { title: 'Desal 1 base cost' },
                  yaxis: { title: 'Disposal cost' },
                  autosize: true,
                  margin: { t: 50, b: 50, l: 50, r: 50 },
                }}
                style={{ width: '100%', height: '100%' }}
              />
            </div>
          </div>
        ))}
      </ResponsiveGridLayout>
    </div>
  );
};

export default Heatmap;
